import axios from 'axios'

const baseUrl = '/api/notes'
/**
 * frontend: localhost:3000
 * backend : localhost:3001/api/notes
 * 
 * But only using this baseUrl, the frontend will try
 * to fetch resources from localhost:3000/api/notes
 * although the real place to fetch from is 
 * localhost:3000/api/notes
 * 
 * Solution: use a proxy:
 */


const getAll = () => {
  const request = axios.get(baseUrl)
  // const nonExisting = {
  //   id: 1000,
  //   content: 'this note is not saved to server',
  //   date: Date.now(),
  //   important: true
  // }
  // return request.then(response => response.data.concat(nonExisting))
  return request.then(response => response.data)
}

const create = newObject => {
  const request = axios.post(baseUrl, newObject)
  return request.then(response => response.data)
}

const update = (id, newObject) => {
  const request = axios.put(`${baseUrl}/${id}`, newObject)
  // console.log(`${baseUrl}/${id}`);
  // console.log(request.then(response => console.log(response.data)));
  return request.then(response => response.data)
}

export default { getAll, create, update }

